/*********************************************************************/
/*****************  Universal Styles  ********************************/
/*********************************************************************/
html {
  width: 100%;
  min-height: 100%;
  background: white;
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  font-family: 'Trebuchet MS', 'Lucida Grande', 'Lucida Sans Unicode', 'Lucida Sans', Tahoma, sans-serif;
  /* Colors  */
}
html body {
  width: 100%;
  min-height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
html a {
  color: #008CBA;
  line-height: inherit;
  text-decoration: none;
}
html li {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  background: transparent;
}
html .center-text {
  text-align: center;
}
html .bold {
  font-weight: bold;
}
html .italic {
  font-style: italic;
}
html .dark-cyan-text {
  color: #42bdc2;
}
html .dark-cyan {
  background: #42bdc2;
}
html .forest-green {
  background: #7fc242;
}
html .orange {
  background: #ff8a3c;
}
html .yellow {
  background: #ffd800;
}
html .dodger-blue {
  background: #388bd1;
}
html .dodger-blue-text {
  color: #388bd1;
}
html .sky-blue {
  background: #a1def8;
}
html .zombie-green {
  background: #3fbf79;
}
html .violet {
  background: #db6ac5;
}
html .mid-blue {
  color: #1954ab;
}
html .light-gray {
  color: #ebebeb;
  background: #ebebeb;
}
html .dark-shpe-blue {
  color: #002266;
}
html .shpe-blue {
  color: #039;
}
html ::-webkit-scrollbar {
  width: 0px;
  /* remove scrollbar space */
  background: transparent;
  /* optional: just make scrollbar invisible */
}
